import {FC, useRef} from "react";
import {Container, InvisibleInput, Row} from "../signup-form/style";
import {FormGroup, Input, Label, Select, Textarea} from "@doar/components";
import {hasKey} from "@doar/shared/dist/methods/index";
import Dropzone from "../Dropzone";
import {StyledDropzoneImages} from "../Dropzone/style";

interface OneProductFormProps{
    errors:any,
    register:any,
    images:any,
    setimages:any,

}


let Form1:FC<OneProductFormProps> = ({
    errors,
    register,
    images,
    setimages,

}) => {
    let fileUploadRef = useRef<HTMLInputElement>(null)
    console.log(images)
    return(
        <Container>
                <Row sm='100' md='100' lg='100' xl='100'>
                    <FormGroup mb='10px'>
                        <Label display="block" mb="5px" htmlFor="name">
                            Product Name
                        </Label>
                        <Input
                            id="name"
                            type="text"
                            placeholder="Enter product name"
                            feedbackText={errors?.name?.message}
                            state={
                                hasKey(errors, "name") ? "error" : "success"
                            }
                            showState={!!hasKey(errors, "name")}
                            {...register("name", {
                                required: "Product name is required",
                            })}
                        />
                    </FormGroup>
                </Row>

                <Row>
                    <FormGroup mb='10px'>
                        <Label display="block" mb="5px" htmlFor="name">
                            Category
                        </Label>
                        <Select
                            id='category'
                            feedbackText={errors?.category?.message}
                            state={hasKey(errors, "category") ? "error" : "success"}
                            showState={!!hasKey(errors, "category")}
                            {...register("category", {
                                required: "Category Options is required",
                            })}
                        >
                            <option hidden={true} value={undefined}>Category</option>
                            <option value='category1' >Category1</option>
                            <option value='category2' >Category2</option>
                            <option value='category3' >Category3</option>
                        </Select>
                    </FormGroup>
                </Row>
                <Row sm='60' md='30' lg='40' xl='40'>
                    <FormGroup mb='10px'>
                        <Label display="block" mb="5px" htmlFor="price">
                            Price
                        </Label>
                        <Input
                            id="price"
                            type="number"
                            placeholder="Enter product price"
                            feedbackText={errors?.price?.message}
                            state={
                                hasKey(errors, "price") ? "error" : "success"
                            }
                            showState={!!hasKey(errors, "price")}
                            {...register("price", {
                                required: "Product price is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row sm='40' md='20' lg='10' xl='10'>
                    <FormGroup mb='10px'>
                        <Label display="block" mb="5px" htmlFor="price_currency">
                            Currency
                        </Label>
                        <Select
                            id='price_currency'
                            feedbackText={errors?.price_currency?.message}
                            state={hasKey(errors, "price_currency") ? "error" : "success"}
                            showState={!!hasKey(errors, "price_currency")}
                            {...register("price_currency", {
                                required: "currency is required",
                            })}
                        >
                            <option selected={true} hidden={true} value=''>Currency</option>
                            <option value='currency1' >currency1</option>
                            <option value='currency2' >currency2</option>
                            <option value='currency3' >currency3</option>
                        </Select>
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb='10px'>
                        <Label display="block" mb="5px" htmlFor="weight">
                            Weight
                        </Label>
                        <Input
                            id="weight"
                            type="number"
                            placeholder="Enter product weight"
                            feedbackText={errors?.weight?.message}
                            state={
                                hasKey(errors, "weight") ? "error" : "success"
                            }
                            showState={!!hasKey(errors, "weight")}
                            {...register("weight", {
                                required: "Product weight is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row>
                    <FormGroup mb='10px'>
                        <Label display="block" mb="5px" htmlFor="amount">
                            Amount
                        </Label>
                        <Input
                            id="amount"
                            type="number"
                            placeholder="Enter product amount"
                            feedbackText={errors?.amount?.message}
                            state={
                                hasKey(errors, "amount") ? "error" : "success"
                            }
                            showState={!!hasKey(errors, "amount")}
                            {...register("amount", {
                                required: "Product amount is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row sm="100" md="100" lg="100" xl="100">
                    <FormGroup mb='10px'>
                        <Label display="block" mb="5px" htmlFor="Description">
                                Description
                        </Label>
                        <Textarea
                            id="description"
                            feedbackText={errors?.description?.message}
                            state={hasKey(errors, "description") ? "error" : "success"}
                            showState={!!hasKey(errors, "description")}
                            {...register("description", {
                                required: "description is required",
                            })}
                        />
                    </FormGroup>
                </Row>
                <Row sm="100" md="100" lg="100" xl="100">
                    <FormGroup mb='20px'>
                        <Label display="block" mb="5px" htmlFor="Description">
                            Images
                        </Label>

                        <Dropzone setimages={(e:any)=>setimages(e)} photoRef={fileUploadRef} many={true} />
                        <Container>
                            {images.map((one:any)=><Row  sm='10' md='10' lg='5' xl='5'>
                                                    <StyledDropzoneImages src={URL.createObjectURL(one)} />
                                                </Row>
                            )}
                        </Container>
                    </FormGroup>
                </Row>
        </Container>
    )
}
export default Form1