import {StyledTitleComponentContainer} from "./style";
import {FC} from "react";
import {StyledTitle} from "../signup-form/style";
import {Button} from "@doar/components";
import {useHistory} from "react-router-dom";

interface StyledTitleComponentProps{
    title:string,
    backText?:any,
    buttonColor?: "white" | "danger" | "primary" | "secondary" | "success" |"warning",
    buttonUrl?:string
}


let StyledTitleComponent:FC<StyledTitleComponentProps> = ({
  title,
  backText,buttonColor,
  buttonUrl
})=>{
    let history = useHistory()
    let handleBack = ()=>{
        if(buttonUrl){
            history.push(buttonUrl)
        }else{
            history.goBack()
        }
    }
    return(
        <StyledTitleComponentContainer>
            <StyledTitle>{title}</StyledTitle>
            <Button onClick={()=>handleBack()} mr='12px' type='button' color={buttonColor || 'danger'}>{backText ? backText : 'Back'}</Button>
        </StyledTitleComponentContainer>
    )
}

export default StyledTitleComponent