import {useParams} from "react-router-dom";
import {Container, Row, StyledWrap} from "../signup-form/style";
import { useForm } from "react-hook-form";
import {Button, FormGroup} from "@doar/components";
import Form1 from "../product_forms/form1";
import {useState} from "react";
import StyledTitleComponent from "../StyledTitleComponent";

interface ParamProps{
    id:string
}


interface IFormValues {
    name: string;
    category: string;
    images: [];
    desc: string;
    price:string,
    price_currency:string,
    weight:number,
    amount:number
}

let OneProductContainer = () =>{
    let {id} = useParams<ParamProps>()
    let [images,setimages] = useState([])
    const {
        register,
        handleSubmit,
        formState: { errors },
    } = useForm();
    const onSubmit = (data: IFormValues) => {
        console.log(JSON.stringify(data, null));
    };

    console.log('Id:',id)
    return <StyledWrap>
        <Container>
            <Row sm="100" md="100" lg="100" xl="100">
                <StyledTitleComponent
                    title='Update the product'
                />
            </Row>
        </Container>
        <form action="#" onSubmit={handleSubmit(onSubmit)} noValidate>
            <Container>
                <Row sm="100" md="100" lg="100" xl="100">
                    <Form1
                        errors={errors}
                        register={register}
                        images={images}
                        setimages={(e:any)=>setimages(e)}
                    />
                    <FormGroup>
                        <Button type="submit" color="brand2" >
                            Update
                        </Button>
                    </FormGroup>
                </Row>
            </Container>
        </form>
    </StyledWrap>
}
export default OneProductContainer