import styled from "@doar/shared/dist/styled";

const defaultColor = '#c0ccda'

interface DropzoneContainerProps{
    height?:string
}


export const StyledDropzoneImages = styled.img`
  margin-top: 5px;
  margin-bottom:5px ;
  width: 50px;
  height: 70px;
  border-style: dashed;
  border: 1px solid ${defaultColor};
`;


export const DropzoneContainer = styled.button<DropzoneContainerProps>`
  height: ${props => props.height || '200px'};
  width: 100%;
  background-color: white;
  border: 1px solid ${defaultColor};
  border-style: dashed;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

`;


export const DropzoneText = styled.div`
  width: 100%;
  color: ${defaultColor};
`;