import {DropzoneContainer, DropzoneText} from "./style";
import {UploadCloud} from 'react-feather'
import {FC} from "react";
import {Container, InvisibleInput, Row} from "../signup-form/style";


interface DropZoneProps{
    photoRef?:any,
    many?:boolean,
    setimages?:any
}


let Dropzone:FC<DropZoneProps> = ({photoRef,many,setimages}) =>{
    let handleClick = ()=>{
        photoRef.current.click()
    }
    return(
        <DropzoneContainer onClick={handleClick} type='button'>
            <InvisibleInput type='file'  multiple={true} ref={photoRef} accept='image/*' onChange={(e:any)=>setimages((prevState:any)=>[...prevState,...e.target.files])} />
            <Container>
                <Row md='100' lg='100' xl='100'>
                    <UploadCloud size={50} color='#c0ccda'/>
                </Row>
                <Row md='100' lg='100' xl='100'>
                    <DropzoneText>Upload {many ? 'photos' :'a photo'}</DropzoneText>
                </Row>
            </Container>
        </DropzoneContainer>
    )
}
export default Dropzone